
.searchPage {
  position: relative;
  min-height: 130vh;
}
.searchPage__header {
    display: flex;
    position: sticky;
    top: 0;
    background-color: white;
    z-index: 100;
    align-items: flex-start;
    padding: 30px;
    border-bottom: 1px solid lightgray;
  }
  
  .searchPage__logo {
    object-fit: contain;
    height: 19px;
    margin-right: 30px;
    border-radius: 0;
    margin-top:15px;
  }
  
  .searchPage__headerBody > .search > .search__input {
    margin-top: 0;
    width: unset;
    margin: unset;
    max-width: unset;
  }
  
  .searchPage__options {
    display: flex;
    align-items: center;
    color: grey;
  }
  
  .searchPage__options a,
  .searchPage__optionsRight a {
    text-decoration: none;
    color: gray;
  }
  
  .searchPage__optionsRight,
  .searchPage__optionsLeft {
    display: flex;
  }
  
  .searchPage__optionsRight {
    margin-left: 80px;
  }
  
  .searchPage__resultLink {
    display: flex;
    align-items: center;
    text-decoration: none;
    color: black;
    margin-bottom: 3px;
  }
  
  .searchPage__option {
    display: flex;
    align-items: center;
    margin-right: 20px;
  }
  
  .searchPage__option a {
    margin-left: 5px;
  }
  
  .searchPage__resultImage {
    object-fit: contain;
    height: 20px;
    width: 20px;
    margin-right: 10px;
  }
  
  .searchPage__result {
    margin: 40px 0;
  }
  
  .searchPage__resultCount {
    color: #70757a;
    font-size: 14px;
  }
  
  .searchPage__results {
    max-width: 650px;
    margin-top: 20px;
    margin-left: 240px;
    margin-bottom: 100px;
  }
  
  .searchPage__resultTitle {
    text-decoration: none;
  }
  
  .searchPage__resultTitle:hover {
    text-decoration: underline;
  }
  
  .searchPage__resultTitle > h2 {
    font-weight: 400;
    color: #1a0dab;
  }
  
  .searchPage__resultSnippet {
    margin-top: 10px;
  }
  
  @media screen and (max-width: 750px) {
    .searchPage__results {
      width: 160%;
      margin-top: 30px;
      margin-left: 110px;
      margin-bottom: 10px;
  ;
    }
  
    .searchPage__optionsRight {
    display: none;
  }
  
  .searchPage__header {
       width: 180%;
    
  }
  
  .searchPage__logo {
  
    height: 30px;
    margin-right: 30px;
    margin-top: 10px;
  
  }
  
  }